.alert {
  padding: 20px; /* Red */
  color: white;
  margin-bottom: 15px;
  font-size: 1.7rem;
  max-width: 274px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--success {
    background-color: #04AA6D;
  }

  &--info {
    background-color: #2196F3;
  }

  &--warning {
    background-color: #ff9800;
  }

  &--error {
    background-color: #f44336;
  }

  &__close-button {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;

    &:hover {
      color: black;
    }
  }
}
